  }
  
  body {
    background-color: #ffffff;
    width: 100%;
    margin: auto;
    font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
      "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
      sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
  }
  header {
    margin-top: 12px;
    margin-bottom: 12px;
    width: 88%;
    margin-left: auto;
    margin-right: auto;
  }
  .header-search {
    display: flex;
    align-items: center;
  }
  .header-search > h1 {
    font-size: 42px;
    font-family: Libre Baskerville;
    font-weight: 500;
    color: #f1651f;
    margin-right: 10px;
  }
  .header-search > h1 a {
    text-decoration: none;
    color: #f1651f;
  }
  .search {
    display: flex;
    flex: 1 1;
    align-items: center;
    border-radius: 24px;
    background-color: #efefef;
    border: 1px solid lightgray;
    transition: all 0.2s ease-in;
    overflow: hidden;
  }
  input:focus,
  textarea:focus,
  select:focus {
    outline: lightgray;
  }
  .search-input {
    font-size: 18px;
    font-weight: lighter;
    font-family: ðŸ‡²ðŸ‡¸;
    letter-spacing: 1.7;
    height: 18px;
    padding: 12px;
    border: none;
    width: 100%;
    margin-left: 16px;
    background-color: #efefef;
  }
  .search > ion-icon {
    font-size: 250%;
    padding: 14px;
    height: 22px !important;
    margin-right: 0px;
